import React, { Component } from 'react';
import FooterTop from '../components/FooterTop';
import FooterBottom from '../components/FooterBottom';

class BudayaKerja extends Component {
  componentWillMount() {
    window.scrollTo(0,0);
  }

  render() {
    return (
    <div>
        <div className="container">
            <div className="WordSection1">
                <p className="MsoNormal"><b><span style={{fontSize: '16.0pt', lineHeight: '106%'}}>&nbsp;</span></b></p>
                <h2 className="title text-center">NILAI-NILAI BUDAYA PERUSAHAN</h2>
                <p className="MsoNormal"><b><span style={{fontSize: '16.0pt', lineHeight: '106%'}}>&nbsp;</span></b></p>
                <p className="MsoNormal"><span style={{color: '#404040'}}>Mengutamakan prinsip yang
                    menjunjung <b>Etika</b>, <b>Moralitas</b>, dan <b>Religiusitas</b>.</span></p>
                <p className="MsoNormal">Dengan Nilai-Nilai Budaya Perusahaan berikut ini: </p>
                <p className="MsoNormal">&nbsp;</p>
                <p className="MsoNormal"><b><span style={{fontSize: '14.0pt', lineHeight: '106%'}}>ORIENTASI
                      HASIL</span></b></p>
                <p className="MsoNormal"><strong><u><span style={{fontFamily: '"Calibri",sans-serif', color: '#404040'}}>Selalu berorientasi hasil</span></u></strong><strong><u><span style={{fontFamily: '"Calibri",sans-serif', color: '#404040', fontWeight: 'normal'}}> <i>(A</i></span></u></strong><strong><i><span style={{fontFamily: '"Calibri",sans-serif', color: '#404040', fontWeight: 'normal'}}>chievement
                        Orientation<span style={{orphans: 2, WebkitTextStrokeWidth: '0px', float: 'none', wordSpacing: '0px'}}>)</span></span></i></strong><b><span style={{color: '#404040'}}>.</span></b><span style={{color: '#404040'}}>&nbsp;Orientasi untuk selalu berprestasi. <br />
                    Kemampuan untuk selalu berusaha melampaui standar atau target yang telah
                    ditetapkan dan fokus pada hasil, serta terus menerus mengolah pikir dan bekerja
                    untuk meraih keunggulan</span> hasil. </p>
                <p className="MsoNormal">&nbsp;</p>
                <p className="MsoNormal"><b><span style={{fontSize: '14.0pt', lineHeight: '106%'}}>KEPUASAN
                      PELANGGAN</span></b></p>
                <p className="MsoNormal"><span style={{color: '#404040'}}>Kepuasan pelanggan <i>(<strong><u><span style={{fontFamily: '"Calibri",sans-serif', fontWeight: 'normal'}}>C</span></u></strong></i></span><i><strong><span style={{fontFamily: '"Calibri",sans-serif', fontWeight: 'normal'}}>ustomer Satisfaction<span style={{orphans: 2, WebkitTextStrokeWidth: '0px', float: 'none', wordSpacing: '0px'}}>)</span></span></strong><b>.</b></i>&nbsp;
                  Kemampuan untuk membantu atau melayani orang lain atau memenuhi kebutuhan
                  pengguna jasa, baik internal maupun eksternal secara tulus dan memuaskan, terutama
                  para Mitra dalam jaringan. </p>
                <p className="MsoNormal"><b><span style={{fontSize: '14.0pt', lineHeight: '106%'}}>&nbsp;</span></b></p>
                <p className="MsoNormal"><b><span style={{fontSize: '14.0pt', lineHeight: '106%'}}>KERJASAMA
                      TIM</span></b></p>
                <p className="MsoNormal"><span style={{color: '#404040'}}>Kerjasama tim <strong><i><u><span style={{fontFamily: '"Calibri",sans-serif', fontWeight: 'normal'}}>(T</span></u></i></strong><strong><i><span style={{fontFamily: '"Calibri",sans-serif', fontWeight: 'normal'}}>eamwork</span></i></strong><i><span style={{orphans: 2, WebkitTextStrokeWidth: '0px', float: 'none', wordSpacing: '0px'}}>)</span></i>.&nbsp;
                    Kemampuan bekerjasama dengan orang lain, baik dalam tim besar maupun tim kecil
                    dalam ruang lingkup organisasi perusahaan.</span></p>
                <p className="MsoNormal">&nbsp;</p>
                <p className="MsoNormal"><b><span style={{fontSize: '14.0pt', lineHeight: '106%'}}>INTEGRITAS</span></b></p>
                <p className="MsoNormal"><span style={{color: '#404040'}}>Integritas <i>(<strong><u><span style={{fontFamily: '"Calibri",sans-serif'}}>I</span></u></strong></i></span><i><strong><span style={{fontFamily: '"Calibri",sans-serif'}}>ntegrity<span style={{orphans: 2, WebkitTextStrokeWidth: '0px', float: 'none', wordSpacing: '0px'}}>)</span></span></strong></i>.
                  Bersatunya kata dengan perbuatan, kemampuan mendeskripsikan maksud, ide dan
                  perasaan serta menterjemahkan seutuhnya dalam perbuatan dengan dilandasi pada
                  ketulusan, kesetiaan, dan tanggungjawab terhadap perusahaan.</p>
                <p className="MsoNormal">&nbsp;</p>
                <p className="MsoNormal"><b><span style={{fontSize: '14.0pt', lineHeight: '106%'}}>OPTIMIS</span></b></p>
                <p className="MsoNormal"><span style={{color: '#404040'}}>Optimis <i>(<strong><u><span style={{fontFamily: '"Calibri",sans-serif', fontWeight: 'normal'}}>O</span></u></strong></i></span><i><strong><span style={{fontFamily: '"Calibri",sans-serif', fontWeight: 'normal'}}>ptimist</span></strong><span style={{orphans: 2, WebkitTextStrokeWidth: '0px', float: 'none', wordSpacing: '0px'}}>)<b>.</b></span></i>
                  Bersemangat dalam bekerja . Selalu berpikir positif apapun peristiwa yang
                  terjadi. Pantang menyerah, tak mudah putus asa dalam menjalankan tugas.</p>
                <p className="MsoNormal">&nbsp;</p>
                <p className="MsoNormal"><b><span style={{fontSize: '14.0pt', lineHeight: '106%'}}>PERBAIKAN
                      BERKELANJUTAN</span></b></p>
                <p className="MsoNormal"><span style={{color: '#404040'}}>Perbaikan berkelanjutan <b><i>(</i></b><strong><i><span style={{fontFamily: '"Calibri",sans-serif', fontWeight: 'normal'}}>Kaize<u><span style={{backgroundClip: 'border-box', backgroundOrigin: 'padding-box', backgroundPositionX: '0%', backgroundPositionY: '0%', backgroundSize: 'auto', borderImageOutset: 0, borderImageRepeat: 'stretch', borderImageSlice: '100%', borderImageSource: 'none', borderImageWidth: 1, boxSizing: 'border-box', outlineColor: 'invert', outlineStyle: 'none', outlineWidth: '0px', backgroundAttachment: 'scroll'}}>N</span>).</u></span></i></strong><strong><span style={{fontFamily: '"Calibri",sans-serif'}}>&nbsp;</span></strong><span style={{orphans: 2, WebkitTextStrokeWidth: '0px', float: 'none', wordSpacing: '0px'}}>
                      Perbaikan terus menerus terhadap setiap pekerjaan yang dilakukan. Selalu
                      berusaha lebih baik dari hari-hari sebelumnya melalui evaluasi dan tindakan
                      koreksi.</span></span></p>
              </div>
        </div>
    <FooterTop />
    <FooterBottom />
    </div>
    );
  }
}
export default BudayaKerja;


